/*global kakao*/ 
import React, { useEffect, useState } from 'react';

declare global {
  interface Window {
    kakao: any;
  }
}

const { kakao } = window;

function Location() {
  const [map, setMap] = useState<any>(null); // map  useState  .

  function setZoomable(zoomable: boolean) {
    if (map) { // map  null   setZoomable  
      map.setZoomable(zoomable);
    }
  }

  useEffect(() => {
    const container = document.getElementById('map');
    const options = {
      center: new kakao.maps.LatLng(37.51323439063145, 127.12231668344003),
      draggable: false,
      level: 3,
    };

    const map = new kakao.maps.Map(container, options);
    setMap(map); // map  state . setZoomable     .

    var markerPosition = new kakao.maps.LatLng(37.51323439063145, 127.12231668344003);
    var marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    var iwContent = `<div style="padding:5px; height:55px;">() <span style="font-weight:100;font-size:1px;" >()05636</span>
         <br><a href="https://map.kakao.com/link/map/(),37.51323439063145,127.12231668344003" style="color:blue" target="_blank"></a>
        <a href="https://map.kakao.com/link/to/(),37.51323439063145,127.12231668344003" style="color:blue" target="_blank"></a></div>`,
      iwPosition = new kakao.maps.LatLng(37.51323439063145, 127.12231668344003);

    var infowindow = new kakao.maps.InfoWindow({
      position: iwPosition,
      content: iwContent,
    });

    infowindow.open(map, marker);

    window.onresize = function () {
      showAsCenterByMarker();
    };

    function showAsCenterByMarker() {
      map.setCenter(marker.getPosition());
    }

    marker.setMap(map);
  }, []);

    return(
        <>
            <div id='map' style={{width: '91%' , height: '650px'}}></div>
            <button style = {{width:'10rem', height:'3rem', marginTop:'2rem', marginRight:'2rem'}} onClick={() => setZoomable(false)}> / </button>
            <button style = {{width:'10rem', height:'3rem', marginTop:'2rem'}} onClick={() => setZoomable(true)}> / </button>
        </>
    )
}

export default Location;